document.addEventListener('DOMContentLoaded', function() {
    buscarCliente();
    crearCliente();
});

function buscarCliente() {
    const ok = document.querySelector('.ok');
    
    ok.addEventListener('click', e => {
        e.preventDefault();

        const body = document.querySelector('.fondo-azul');
        const overflow = document.querySelector('.overflow');

        body.removeChild(overflow); // Eliminar elemento del DOM
        body.classList.remove('fijar-body');

    });
}


function crearCliente() {
    const addClient = document.querySelector('.add-section');

    addClient.addEventListener('click', e => {
        e.preventDefault();

        const noShowForm = document.querySelector('.overflow');
        // if(noShowForm.classList.contains('overflow-noshow')){
        //     noShowForm.classList.remove('overflow-noshow');
        // return;
        // }
        // noShowForm.classList.add('overflow-noshow');

        noShowForm.classList.toggle('overflow-noshow'); // Hace las lineas comentadas

    });
}
